import Cartpage from "./components/CartPage.js";
import ProductDetailPage from "./components/ProductDetailPage.js";
import ProductListPage from "./components/ProductListPage.js";
import { init } from "./router.js";


export default function App({$app}){
    this.route = () => {
        const { pathname } = location;

        if(pathname === '/'){
            new ProductListPage({$app}).render();
        }else if(pathname.indexOf('/products/') === 0){
            const [, , productId] = pathname.split('/');
            new ProductDetailPage({
                $app,
                productId,
            }).render();
        }else if(pathname.indexOf('/cart') === 0){
            new Cartpage({
                $app
            }).render();
        }
    }

    //ROUTE_CHANGE Ïù¥Î≤§Ìä∏ Î∞úÏÉù Ïãú ÎßàÎã§ AppÏùò this.route Ìï®ÏàòÍ∞Ä Ìò∏Ï∂úÎêòÍ≤å ÌïòÎäî Ìö®Í≥º
    init(this.route);


    //Îí§Î°úÍ∞ÄÍ∏∞, ÏïûÏúºÎ°úÍ∞ÄÍ∏∞ Î∞úÏÉù Ïãú popstate Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌï©ÎãàÎã§.
    window.addEventListener("popstate", this.route);

    this.route();
}